import React, {Component} from 'react'

export default class Home extends Component{
    render(){
        return (
            <div className='container grid-1280'>
                <div className='columns'>
                    <div className='column'>
                        <h2>Trabalho de Minerao de Dados</h2>
                        <h4> <small>Sistemas de Informao Distribuidos - CK107</small></h4>

                        <h4>Sobre a Execuo do Trabalho</h4>
                        <p>
                            Antes da anlise dos dados foram feitas alguns ajustes para que fosse possvel trat-los dentro do spark,
                            como converso de datas para o formato timestamp, alem da excluso de algumas tags dos campos de texto que causaram alguns erros durante o parse.
                        </p>

                        <h4>Equipe</h4>
                        <ul>
                            <li>Joo Victor</li>
                            <li>Esdras Lemos</li>
                            <li>Stenio Wagner</li>
                            <li>Csar Rodrigues</li>
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}


